/* Reset default browser styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body styling with background gradient */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(to right, #2c3e50, #4ca1af); /* A modern gradient */
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin: 0;
}

/* General container for all forms and content */
.container {
  background-color: white;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Softer shadow for modern look */
  width: 100%;
  max-width: 400px; /* Limits the width on larger screens */
  text-align: center;
}

/* Headings */
h1 {
  color: #333;  /* Strong dark color for contrast */
  margin-bottom: 20px;
  font-size: 2rem; /* Larger heading for modern look */
}

/* Labels for form inputs */
label {
  display: block;
  text-align: left;
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
}

/* Input field styling */
input {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 1rem;
  color: #333;
  background-color: #f7f7f7; /* Light background for modern input field */
  transition: border-color 0.3s ease; /* Smooth border transition */
}

input:focus {
  border-color: #4ca1af; /* Highlight border on focus */
  outline: none;
}

/* Button styling */
button {
  width: 100%;
  padding: 12px;
  background-color: #4ca1af;
  border: none;
  color: white;
  font-size: 1.1rem;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease; /* Smooth hover effect */
}

button:hover {
  background-color: #3b8d99; /* Slightly darker on hover */
}

/* Footer styling */
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: #1c2833;
  color: #cccccc;
  padding: 15px;
  text-align: center;
  font-size: 0.9rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.footer p {
  margin: 5px 0;
}

/* Styling for error messages */
.error-message {
  color: #ff4c4c;
  font-weight: bold;
}

/* Welcome message when logged in */
.welcome-message {
  background-color: white;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); 
}

.welcome-message p {
  color: #333;
  font-weight: bold;
  margin: 10px 0;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  body {
    padding: 10px;  /* More compact padding on mobile */
  }

  .container {
    padding: 20px;  /* Smaller padding for mobile */
    max-width: 100%;  /* Full width for mobile */
  }

  .company-logo {
    width: 120px;  /* Smaller logo on mobile */
  }

  h1 {
    font-size: 1.5rem;  /* Slightly smaller heading on mobile */
  }

  input, button {
    font-size: 1rem;  /* Adjust input and button size for mobile */
    padding: 10px;  /* Slightly smaller padding on mobile */
  }

  .footer {
    font-size: 0.8rem;  /* Slightly smaller text for the footer */
    padding: 10px;
  }
}
